  table.availability-grid tbody tr div {
    display: block;
    min-height: 1em;
    position: relative; }
  table.availability-grid tbody tr input[type="checkbox"] {
    appearance: none;
    background: transparent;
    position: absolute;
    z-index: 99999;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;
    cursor: pointer; }
  table.availability-grid tbody tr div {
    background: #F9FAFA;
    border: 1px solid #d8dede;
    transition: all 0.30s ease-in-out;
    outline: none; }
  table.availability-grid tbody tr div:focus, table.availability-grid tbody tr div:active {
    background: #d8dede; }
  table.availability-grid tbody tr input[type="checkbox"]:focus ~ div {
    background: #e3e7e7;
    box-shadow: 0 0 5px #51cbee;
    border: 1px solid #51cbee; }
  table.availability-grid tbody tr input[type="checkbox"]:checked ~ div {
    background: #64D481;
    border-color: #404853; }
  table.availability-grid tbody tr input[type="checkbox"]:checked:focus ~ div {
    background: #88de9e;
    border-color: #51cbee; }
